<template>
	<div class="userSafe">
		<!--账号与安全返回-->
		<div class="userSafe-back">
			<div class="left" @click="backGo"><img src="/img/wode/shezhi/shezhi-back.png" alt="" /></div>
			<div class="right">用户与安全</div>
		</div>
		<!--账号与安全功能-->
		<div class="userSafe-gongneng">
			<ul>
				<li class="item">
					<div class="gongneng-left">昵称</div>
					<div class="gongneng-right" v-text="this.$store.state.user.username"></div>
				</li>
				<li v-for="(item,index) in userSafeData" class="item">
					<div class="item-left">{{ item }}</div>
					<div class="item-right">
						<div v-if="index == 0" @click="xgsjhm">
							<img src="/img/wode/jichu/wode-back.png" alt="" />
						</div>
						<div v-else-if="index == 1" @click="xgdlmm">
							<img src="/img/wode/jichu/wode-back.png" alt="" />
						</div>
						<div v-else-if="index == 2" @click="dsfbd">
							<img src="/img/wode/jichu/wode-back.png" alt="" />
						</div>
						<div v-else-if="index == 3" @click="zxzh">
							<img src="/img/wode/jichu/wode-back.png" alt="" />
						</div>
					</div>
				</li>
			</ul>
		</div>
		<!-- 弹出层-->
		<div class="tanchuceng" v-show="show"></div>
		<div class="tanchu-logo" v-show="show"><img src="/img/shezhi-logo.png" alt="" /></div>
		<div class="tanchu" v-show="show">
			<div class="tanchu-top">
				<div class="tanchu-title">温馨提示</div>
				<div class="tanchu-content">您确定要注销账户吗？</div>
			</div>
			<div class="tanchu-bottom">
				<div @click="tanChuFalse" class="tanchu-left">我再想想</div>
				<div @click="tanChuEnd" class="tanchu-right">狠心离开</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: "userSafe",
		data:function(){
			return {
				userSafeData: [
					"修改手机号码",
					"修改登录密码",
					"第三方绑定",
					"注销账户"
				],
				user: '',
				show:false
			}
		},
		mounted(){
			this.user = this.$store.state.user.username
		},
		methods: {
			// 修改手机号码
			xgsjhm(){
				console.log('修改手机号码')
				this.$router.push('upPhone')
			},
			// 修改登录密码
			xgdlmm(){
				console.log('修改登录密码')
          this.$router.push("/upPassword")
			},
			// 第三方绑定
			dsfbd(){
				console.log('第三方绑定')
			},
			// 注销登录
			zxzh(){
				console.log('注销登录')
				this.show = true
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = 'hidden'
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 我再想想
			tanChuFalse(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层 狠心离开
			tanChuEnd(){
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
				this.$store.commit('clearUser')
				this.$router.push('/')
			},
			backGo(){
				this.$router.go(-1)
			}
		}
	}
</script>

<style>
	.userSafe{
		width:100%;
		height:100%;
		padding:0 30px;
	}
	/*设置返回*/
	.userSafe .userSafe-back{
		display: flex;
		justify-content: space-between;
		font-size:22px;
		line-height:90px;
		text-align: center;
	}
	.userSafe .userSafe-back .right{
		width:100%;
		font-size: 36px;
    color: #1b1b1b;
	}
	/*账号与安全功能*/
	.userSafe .userSafe-gongneng{
		width:100%;
		height:100%;
	}
	.userSafe .item{
		font-size:26px;
    color: #1b1b1b;
		line-height:60px;
		padding: 20px 0;
		display: flex;
		justify-content: space-between;
    border-bottom: 1px solid #eeeeee;
	}
	.userSafe .gongneng-right{
		width:300px;
		height:40px;
    margin-top: 10px;
    line-height: 40px;
		background-color:#eeeeee;
		border-radius:10px;
		text-align: center;
		color:#98D15C;
	}
	/*  弹出层*/

	.userSafe .tanchuceng {
		width: 100%;
		height: 1034px;
		background-color: #c0c0c0;
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0.4;
	}
	.tanchu-logo{
		position:absolute;
		left:314px;
		top:185px;
		/*z-index:0px;*/
	}
	.tanchu {
		width: 500px;
		height: 300px;
		background-color: white;
		position: fixed;
		left: 125px;
		top: 275px;
	}
	.tanchu .tanchu-top{
		padding: 0 30px;
	}
	.tanchu .tanchu-title{
		width:100%;
		font-size:26px;
		color:#84c225;
		padding:40px 0;
		text-align: center;
		border-bottom: 1px solid #84c225;
	}
	.tanchu .tanchu-content{
		width:100%;
		font-size:20px;
		color:#333333;
		padding:40px 0 40px 20px;
	}
	.tanchu .tanchu-bottom{
		width:100%;
		height:80px;
		display: flex;
		justify-content: space-between;
		border-top:1px solid #f4f4f4;
	}
	.tanchu .tanchu-bottom div{
		width:50%;
		height:100%;
		font-size:20px;
		line-height:80px;
		text-align: center;
	}
	.tanchu .tanchu-bottom .tanchu-left{
		color:#84c225;
	}
	.tanchu .tanchu-bottom .tanchu-right{
		color:#a9a6a9;
	}
</style>
